
.flex_container{
    display: inline-flex;
    flex: wrap;
margin-top: 10vh;
margin-left: 10vw;
flex-direction: row;
/* background-color: black; */
animation-name: example;
  animation-duration: 3s;
animation-iteration-count: infinite;

}

.flex_container >.card_body{
    font-size: .9vw;
    text-transform: capitalize;
    /* background-color: black; */
    color: white;
  width: 16vw;
    
}


    @keyframes example {
        10%{filter: drop-shadow(16px 16px 20px green) invert(20%);}
      
    20%{filter: drop-shadow(16px 16px 20px red) invert(0%);}
    30%{filter: drop-shadow(16px 16px 20px orange) invert(0%);}
    40%{filter: drop-shadow(16px 16px 20px yellow) invert(0%);}
    50%{filter: drop-shadow(16px 16px 20px pink) invert(0%);}
    60%{filter: drop-shadow(16px 16px 20px blue) invert(0%);}
    70%{filter: drop-shadow(16px 16px 20px purple) invert(0%);}
    80%{filter: drop-shadow(16px 16px 20px brown) invert(0%);}
    90%{filter: drop-shadow(30px 16px 20px red) invert(10%);}
    
      }


.title{
   text-align: center;
   background-color: black;
   color: white;
}







